import * as API from 'api';
import * as storage from 'storage';
import { Plant } from 'types';
import { t } from 'utils';
import browser from 'webextension-polyfill';
import { composeTrees } from './helpers';

type Props = { isSuccess: boolean };

export default async function onPlantingOver({ isSuccess }: Props) {
  if (isSuccess) {
    browser.notifications.create({
      type: 'basic',
      title: t('notification_title_on_success'),
      message: t('notification_text_on_success'),
      iconUrl: require('images/forest.svg'),
    });
  }

  const { growingPlant, user } = await storage.get(['growingPlant', 'user']);
  if (growingPlant === null) return;

  const dateNow = new Date();
  const targetDuration =
    (new Date(growingPlant.endTime).getTime() -
      new Date(growingPlant.startTime).getTime()) /
    1000;
  const actualDuration =
    (dateNow.getTime() - new Date(growingPlant.startTime).getTime()) / 1000;

  const resultPlant: Plant = {
    ...growingPlant,
    isSuccess,
    ...(isSuccess === false && {
      endTime: dateNow.toISOString(),
    }),
    updatedAt: dateNow.toISOString(),
    trees: composeTrees(
      targetDuration,
      isSuccess ? targetDuration : actualDuration,
      growingPlant.treeTypeGid
    ),
  };

  storage.set({ growingPlant: null, resultPlant });

  if (user) {
    try {
      const validatedPlant = await API.createPlant(resultPlant);
      await storage.setData('plantRecords', (plantRecords) => [
        {
          plant: validatedPlant,
          syncStatus: 'Succeeded',
        },
        ...plantRecords,
      ]);
    } catch (err) {
      await storage.setData('plantRecords', (plantRecords) => [
        { plant: resultPlant, syncStatus: 'Failed' },
        ...plantRecords,
      ]);
    }
    const data = await API.validateUser(user.id).catch(() => null);
    if (data) await storage.set(data);
  } else {
    await storage.setData('plantRecords', (plantRecords) => [
      { plant: resultPlant, syncStatus: 'Pending' },
      ...plantRecords,
    ]);
  }
}
