import plants from '@forest/plants';
import { plantIdsToHideIfLocked } from 'const';
import * as storage from 'storage';
import { PlantingStatus, Tree } from 'types';
import { calcTreesCount } from 'utils';

export const persistState = async () => {
  const {
    growingPlant,
    selectedPlantIndex,
    unlockedPlantIds,
    serverRegion,
    selectedPlantingDuration,
  } = await storage.get([
    'unlockedPlantIds',
    'selectedPlantIndex',
    'growingPlant',
    'serverRegion',
    'selectedPlantingDuration',
  ]);
  const plantIndex = (() => {
    const plantId = plants[selectedPlantIndex].id;
    return unlockedPlantIds.includes(plantId) === false &&
      plantIdsToHideIfLocked.includes(plantId)
      ? 0
      : selectedPlantIndex;
  })();
  const [plantingStatus, timerResumable] = ((): [
    PlantingStatus,
    null | string
  ] => {
    if (growingPlant === null) return ['Idle', null];
    const timeRemaining = Math.round(
      (new Date(growingPlant.endTime).getTime() - Date.now()) / 1000
    );
    return timeRemaining > 0
      ? ['Started', growingPlant.endTime]
      : ['Succeeded', null];
  })();

  return {
    plantIndex,
    plantingStatus,
    timerResumable,
    unlockedPlantIds,
    serverRegion,
    selectedPlantingDuration,
  };
};

export const composeTrees = (
  targetDuration: number,
  actualDuration: number,
  plantId: number
) => {
  const isDead = actualDuration < targetDuration;
  const targetTreesCount = calcTreesCount(targetDuration);
  const count = calcTreesCount(actualDuration);
  const grownTreesCount = count === 1 && isDead ? 0 : count;
  const grownTrees: Tree[] = [...Array(grownTreesCount)].map((_, index) => ({
    isDead: false,
    phase: index + 4,
    treeType: plantId,
  }));
  const deadTrees: Tree[] = [...Array(targetTreesCount - grownTreesCount)].map(
    () => ({
      isDead: true,
      phase: 8,
      treeType: plantId,
    })
  );
  return [...grownTrees, ...deadTrees];
};

export const calcPlantPhase = (
  timeRemaining: number,
  targetDuration: number,
  phaseType: 'Four' | 'Seven'
) => {
  const pastTime = targetDuration - timeRemaining;
  const maxPhase =
    phaseType === 'Four'
      ? 4
      : Math.max(Math.floor(targetDuration / 1800) + 3, 4);
  const phase =
    pastTime < targetDuration
      ? Math.floor((maxPhase * pastTime) / targetDuration) || 1
      : maxPhase;
  return phase as 1 | 2 | 3 | 4 | 5 | 6 | 7;
};

export const getPlantModel = ({
  unlockedPlantIds,
  plantIndex,
  plantingStatus,
  timeRemaining,
  targetDuration,
}: {
  unlockedPlantIds: number[];
  plantIndex: number;
  plantingStatus: PlantingStatus;
  timeRemaining: number;
  targetDuration: number;
}) => {
  const currentPlant = plants[plantIndex];
  const currentPlantImage = (function getCurrentPlantImage() {
    if (plantingStatus === 'Failed') return currentPlant.images.dead;
    if (plantingStatus === 'Started') {
      return currentPlant.images[
        calcPlantPhase(
          timeRemaining,
          targetDuration,
          currentPlant.phaseType
        ) as keyof typeof currentPlant.images
      ];
    }

    if (currentPlant.phaseType === 'Four') return currentPlant.images[4];

    return currentPlant.images[
      (calcTreesCount(timeRemaining) + 3) as 4 | 5 | 6 | 7
    ];
  })();

  return {
    id: currentPlant.id,
    image: currentPlantImage,
    isLocked: unlockedPlantIds.includes(currentPlant.id) === false,
  };
};
