/* eslint-disable react-hooks/rules-of-hooks */
import plants from '@forest/plants';
import * as API from 'api';
import { AxiosError } from 'axios';
import { afterChinaMigration, onChinaMigration } from 'china-migration';
import {
  defaultTags,
  defaultUnlockedPlantIds,
  plantIdsToHideIfLocked,
  Port,
} from 'const';
import type { Message } from 'messages';
import * as storage from 'storage';
import {
  ContentProps,
  GrowingPlant,
  OptionsProps,
  PlantingStatus,
  PopupProps,
} from 'types';
import { postMessage, sendMessage, sendTabsMessage } from 'utils';
import browser from 'webextension-polyfill';
import setupDevelopment from './dev';
import { getPlantModel, persistState } from './helpers';
import onPlantingOver from './onPlantingOver';
import useState from './state';
import createTimer from './timer';

process.env['NODE_ENV'] === 'development' && setupDevelopment();

let contentPorts: browser.Runtime.Port[] = [];
let popupPort: null | browser.Runtime.Port = null;
let unlockedPlantIds = defaultUnlockedPlantIds;

(async function main() {
  await storage.migrate();
  const persistedState = await persistState();
  unlockedPlantIds = persistedState.unlockedPlantIds;
  API.setAPIBaseURLByServerRegion(persistedState.serverRegion);

  const [getPlantIndex, setPlantIndex] = useState<number>(
    persistedState.plantIndex,
    (plantIndex) => {
      storage.setData('selectedPlantIndex', plantIndex);
      const plant = getPlantModel({
        unlockedPlantIds,
        plantIndex,
        plantingStatus: getStatus(),
        targetDuration: timer.getTargetDuration(),
        timeRemaining: timer.getTimeRemaining(),
      });
      postMessage(popupPort, { type: 'UPDATE_PLANT', payload: plant });
      contentPorts.forEach((port) => {
        postMessage(port, { type: 'UPDATE_PLANT', payload: plant });
      });
    },
    false
  );

  const timer = createTimer({
    initialTargetDuration: persistedState.selectedPlantingDuration,
    resumable: persistedState.timerResumable,
    onTargetDurationChange: (targetDuration) =>
      storage.setData('selectedPlantingDuration', targetDuration),
    onTimeRemainingChange: (timeRemaining) => {
      const plant = getPlantModel({
        plantIndex: getPlantIndex(),
        plantingStatus: getStatus(),
        targetDuration: timer.getTargetDuration(),
        timeRemaining,
        unlockedPlantIds,
      });
      postMessage(popupPort, {
        type: 'UPDATE_PLANT_AND_TIME_REMAINING',
        payload: { plant, timeRemaining },
      });
      contentPorts.forEach((port) => {
        postMessage(port, {
          type: 'UPDATE_PLANT_AND_TIME_REMAINING',
          payload: { plant, timeRemaining },
        });
      });
    },
    onTimeUp: () => setStatus('Succeeded'),
  });

  const [getStatus, setStatus] = useState<PlantingStatus>(
    persistedState.plantingStatus,
    (status) => {
      sendMessage({ type: 'UPDATE_PLANTING_STATUS', payload: status });
      sendTabsMessage({ type: 'UPDATE_PLANTING_STATUS', payload: status });
      switch (status) {
        case 'Succeeded':
          browser.browserAction.setIcon({ path: 'logo/dotted.png' });
          return onPlantingOver({ isSuccess: true });
        case 'Failed':
          timer.stop();
          return onPlantingOver({ isSuccess: false });
        case 'Idle': {
          browser.browserAction.setIcon({ path: 'logo/128.png' });
          const plant = getPlantModel({
            plantIndex: getPlantIndex(),
            plantingStatus: 'Idle',
            targetDuration: timer.getTargetDuration(),
            timeRemaining: timer.getTargetDuration(),
            unlockedPlantIds,
          });
          return postMessage(popupPort, {
            type: 'UPDATE_PLANT',
            payload: plant,
          });
        }
      }
    }
  );

  const toNextPlant = (currentIndex: number = getPlantIndex()): void => {
    const nextIndex = currentIndex + 1;
    if (nextIndex === plants.length) {
      return setPlantIndex(0);
    }

    const nextPlantId = plants[nextIndex].id;
    if (
      unlockedPlantIds.includes(nextPlantId) === false &&
      plantIdsToHideIfLocked.includes(nextPlantId)
    ) {
      return toNextPlant(nextIndex);
    }

    setPlantIndex(nextIndex);
  };

  const toPrevPlant = (currentIndex: number = getPlantIndex()): void => {
    const prevIndex = currentIndex - 1;
    if (prevIndex < 0) {
      const lastPlantIndex = plants.length - 1;
      const lastPlantId = plants[lastPlantIndex].id;

      if (
        unlockedPlantIds.includes(lastPlantId) === false &&
        plantIdsToHideIfLocked.includes(lastPlantId)
      ) {
        return toPrevPlant(lastPlantIndex);
      }

      return setPlantIndex(lastPlantIndex);
    }

    const prevPlant = plants[prevIndex];
    if (
      unlockedPlantIds.includes(prevPlant.id) === false &&
      plantIdsToHideIfLocked.includes(prevPlant.id)
    ) {
      return toPrevPlant(prevIndex);
    }

    setPlantIndex(prevIndex);
  };

  const replyPopupProps = async (): Promise<PopupProps> => {
    const siteBlockingMode = await storage.getData('siteBlockingMode');
    const siteList = await storage.getData(
      siteBlockingMode === 'Allow' ? 'allowList' : 'blockList'
    );
    const timeRemaining = timer.getTimeRemaining();
    const targetDuration = timer.getTargetDuration();
    const plantingStatus = getStatus();
    const plant = getPlantModel({
      unlockedPlantIds,
      plantIndex: getPlantIndex(),
      plantingStatus,
      targetDuration,
      timeRemaining,
    });
    return {
      plant,
      siteBlockingMode,
      plantingStatus,
      timeRemaining,
      siteList,
    };
  };

  const replyContentProps = async (): Promise<ContentProps> => {
    const siteBlockingMode = await storage.getData('siteBlockingMode');
    const timeRemaining = timer.getTimeRemaining();
    const plantingStatus = getStatus();
    const plant = getPlantModel({
      unlockedPlantIds,
      plantIndex: getPlantIndex(),
      plantingStatus,
      targetDuration: timer.getTargetDuration(),
      timeRemaining,
    });
    return {
      siteBlockingMode,
      plantingStatus,
      timeRemaining,
      plant,
    };
  };

  const replyOptionsProps = async (): Promise<OptionsProps> => {
    const data = await storage.get([
      'allowList',
      'blockList',
      'user',
      'serverRegion',
      'isInChinaMigration',
    ]);
    return {
      ...data,
      plantingStatus: getStatus(),
    };
  };

  const startPlanting = async (plantId: number) => {
    const plantingDuration = timer.getTimeRemaining();
    const startTimeDate = new Date();
    const endTime = new Date(
      startTimeDate.getTime() + plantingDuration * 1000
    ).toISOString();

    const growingPlant: GrowingPlant = {
      treeTypeGid: plantId,
      startTime: startTimeDate.toISOString(),
      endTime,
      tag: 0,
      note: '',
    };

    storage.setData('growingPlant', growingPlant);
    timer.start(endTime);
  };

  browser.runtime.onConnect.addListener((port) => {
    switch (port.name) {
      case Port.Popup:
        popupPort = port;
        popupPort.onDisconnect.addListener(() => (popupPort = null));
        popupPort.onMessage.addListener(async (message: Message) => {
          if (popupPort === null) return;
          switch (message.type) {
            case 'MODIFY_TIME':
              return timer.setTargetTime(message.payload.method);
            case 'MODIFY_PLANT_INDEX':
              return message.payload === 'Next' ? toNextPlant() : toPrevPlant();
            case 'START_PLANTING':
              setStatus('Started');
              return startPlanting(message.payload.plantId);
            case 'BACK_TO_IDLE':
              return setStatus('Idle');
          }
        });
        break;
      case Port.Content:
        contentPorts.push(port);
        port.onDisconnect.addListener((port) => {
          contentPorts = contentPorts.filter((p) => p !== port);
        });
        port.onMessage.addListener((message: Message) => {
          switch (message.type) {
            case 'GIVE_UP':
              return setStatus('Failed');
          }
        });
        break;
    }
  });

  browser.runtime.onMessage.addListener((message: Message) => {
    switch (message.type) {
      case 'GET_POPUP_PROPS':
        return replyPopupProps();
      case 'GET_CONTENT_PROPS':
        return replyContentProps();
      case 'GET_OPTIONS_PROPS':
        return replyOptionsProps();
    }
  });

  (async function executeContentScript() {
    const tabs = await browser.tabs.query({
      url: ['https://*/*', 'http://*/*'],
    });
    tabs.forEach((tab) => {
      browser.tabs
        .executeScript(tab.id, { file: '/content.js' })
        .catch(() => {});
    });
  })();

  browser.storage.onChanged.addListener((changes) => {
    if (changes.unlockedPlantIds) {
      unlockedPlantIds = changes.unlockedPlantIds.newValue;
    }
  });

  const revalidateUser = async () => {
    const user = await storage.getData('user');
    if (user === null) return;
    try {
      const data = await API.validateUser(user.id);
      storage.set(data);
    } catch (err) {
      const error = err as AxiosError;
      if (error?.response?.status === 488) onChinaMigration();
      if (error?.response?.status === 481) {
        /**
         * TODO: With requesting the getCurrentUserId, server should send a response
         * back which contains a set-cookie header to set credential for China server
         */
        // API.setAPIBaseURLByServerRegion('China');
        // const newUserId = await API.getCurrentUserId(user.rememberToken);
        // const data = await API.validateUser(newUserId);
        // storage.set({ ...data, isInChinaMigration: false, serverRegion: 'China' });
        await afterChinaMigration();
        await browser.runtime.openOptionsPage();
        return sendMessage({ type: 'RELOAD_OPTIONS_PAGE', payload: null });
      }

      if (error?.response?.status === 403) {
        const plantRecords = await storage.getData('plantRecords');
        await storage.set({
          user: null,
          tags: defaultTags,
          unlockedPlantIds: defaultUnlockedPlantIds,
          plantRecords: plantRecords.filter(
            ({ plant }) => plant.id === undefined
          ),
        });
        await browser.runtime.openOptionsPage();
        return sendMessage({ type: 'RELOAD_OPTIONS_PAGE', payload: null });
      }
    }
  };
  revalidateUser();
  // setInterval(revalidateUser, 150000);
})();
