// Base on https://github.com/xpl/crx-hotreload
import browser from 'webextension-polyfill';

type File = {
  name: string;
  lastModifiedDate: string;
};

type EntriesReader = {
  readEntries: (callback: (entries: DirectoryEntry[]) => void) => void;
};

type DirectoryEntry = {
  createReader: () => EntriesReader;
  isDirectory: boolean;
  name: string;
  file: (callback: (file: File) => void) => void;
};

type ExtendedBrowserRuntime = browser.Runtime.Static & {
  getPackageDirectoryEntry: (
    callback: (dirEntry: DirectoryEntry) => void
  ) => void;
};

const getFilesInDir = (dirEntry: DirectoryEntry) =>
  new Promise<File[]>((resolve) => {
    dirEntry.createReader().readEntries(async (entries) => {
      const files = await entries
        .filter((entry) => entry.name[0] !== '.')
        .reduce(async (prevTask, entry) => {
          const files = await prevTask;

          if (entry.isDirectory) {
            return [...files, ...(await getFilesInDir(entry))];
          }

          const file = await new Promise<File>((resolve) =>
            entry.file(resolve)
          );
          return [...files, file];
        }, Promise.resolve([] as File[]));
      resolve(files);
    });
  });

const genBundleId = async (dirEntry: DirectoryEntry) => {
  const files = await getFilesInDir(dirEntry);
  return files.map((file) => file.name + file.lastModifiedDate).join();
};

const watchChanges = async (
  dirEntry: DirectoryEntry,
  prevBundleId: string | null = null
) => {
  const bundleId = await genBundleId(dirEntry);

  if (prevBundleId === null || prevBundleId === bundleId) {
    // retry after 1s
    setTimeout(() => watchChanges(dirEntry, bundleId), 1000);
  } else {
    setTimeout(() => {
      browser.runtime.openOptionsPage();
    }, 1000);
    browser.runtime.reload();
  }
};

export default async function useHotReload() {
  (browser.runtime as ExtendedBrowserRuntime).getPackageDirectoryEntry(
    (dirEntry) => watchChanges(dirEntry)
  );
  const tabs = await browser.tabs.query({
    active: true,
    lastFocusedWindow: true,
  });
  tabs.forEach((tab) => {
    browser.tabs.reload(tab.id);
  });
}
