/* eslint-disable react-hooks/rules-of-hooks */
import { TimeModifyMethod } from 'types';
import useState from './state';

type CreateTimerProps = {
  initialTargetDuration: number;
  onTimeRemainingChange: (timeRemaining: number) => void;
  onTargetDurationChange?: (targetDuration: number) => void;
  onTimeUp: () => void;
  resumable: string | null;
};

export default function createTimer({
  initialTargetDuration,
  onTargetDurationChange,
  onTimeRemainingChange,
  onTimeUp,
  resumable,
}: CreateTimerProps) {
  let targetDuration = initialTargetDuration;
  const [getTime, setTime] = useState(
    initialTargetDuration,
    onTimeRemainingChange,
    false
  );
  const minTime = 600;
  const maxTime = 7200;
  let timerId: null | number = null;
  let msEndTime = 0;

  const reset = () => {
    setTime(targetDuration);
    timerId = null;
  };

  const setTargetTime = (method: TimeModifyMethod): void => {
    switch (method) {
      case 'Increase':
        if (targetDuration === maxTime) return;
        targetDuration =
          targetDuration === maxTime ? maxTime : targetDuration + 300;
        break;

      case 'Decrease':
        if (targetDuration === minTime) return;
        targetDuration =
          targetDuration === minTime ? minTime : targetDuration - 300;
        break;
    }

    onTargetDurationChange?.(targetDuration);
    setTime(targetDuration);
  };

  const stop = (): void => {
    clearInterval(timerId as number);
    reset();
  };

  const countDown = (): void => {
    const timeRemaining = Math.round((msEndTime - Date.now()) / 1000);

    if (timeRemaining < 0) {
      onTimeUp();
      setTime(0);
      stop();
      return;
    }

    setTime(timeRemaining);
  };

  const start = (endTimeISOString: string) => {
    if (timerId === null) {
      msEndTime = new Date(endTimeISOString).getTime();

      if (process.env['NODE_ENV'] === 'development') {
        msEndTime = Date.now() + 5000;
      }

      timerId = window.setInterval(countDown, 1000);
    }
  };

  resumable && start(resumable);

  return {
    getTimeRemaining: getTime,
    getTargetDuration: () => targetDuration,
    setTargetTime,
    start,
    stop,
  };
}
